.button {
  height: 30px;
  width: 120px;
  text-align: center;
  background-color: rgb(222, 92, 92);
  border-radius: 5px;
  padding-top: 1px;
  margin-top: 5px;
  color: aliceblue;
  text-decoration: none;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  margin-left: 60px;
}

.button:hover {
  background-color: rgb(246, 87, 87);
}

