.outerdiv {
  display: inline-block;
  margin-left: 30px;
  margin-top: 10px;
  margin-bottom: 10px;
  /* transition: all 0.2s ease-in; */
}

/* .outerdiv:hover {
  transform: scale(1.1);
  opacity: 0.5;
} */

.card:hover {
  transform: scale(1.07);
  opacity: 0.5;
}

.card {
  background-color: rgb(241, 241, 72);
  height: 270px;
  width: 170px;
  border: 4px solid white;
  border-radius: 10px;
  transition: all 0.2s ease-in;
}

.top {
  padding-left: 5px;
  font-size: 30px;
  color: white;
}

.bottom {
  text-align: end;
  font-size: 30px;
  margin-top: 55px;
  margin-right: 5px;
  color: white;
}

.mid {
  /* font-size: 60px; */
  text-align: center;
  margin-top: 45px;
  font-size: 90px;
  font-weight: 900;
  color: white;
}
