.topcard {
  display: inline-block;
  position: fixed;
  margin: 20px;
  top: 20px;
  right: 10px;
}
.outerdiv {
  display: inline-block;
  margin-left: 30px;
  margin-top: 10px;
  margin-bottom: 10px;
  animation: 2.3s ease 0s infinite beat;
}

/* .outerdiv:hover {
    transform: scale(0.9);
    opacity: 0.5;
  } */

.card {
  background-color: rgb(241, 241, 72);
  height: 270px;
  width: 170px;
  border: 4px solid white;
  border-radius: 10px;
}

.top {
  padding-left: 5px;
  font-size: 30px;
  color: white;
}

.bottom {
  text-align: end;
  font-size: 30px;
  margin-top: 55px;
  margin-right: 5px;
  color: white;
}

.mid {
  /* font-size: 60px; */
  text-align: center;
  margin-top: 45px;
  font-size: 90px;
  font-weight: 900;
  color: white;
}

.numberbg {
  height: 130px;
  width: 120px;
  background-color: #0f9999;
  color: rgb(134, 249, 255);
  border-radius: 18px;
  text-align: center;
  font-weight: 600;
  margin-top: 70px;
  margin-left: 60px;
  border-radius: 4px solid white;
  /* padding-top: 20px; */
}

.number {
  margin-top: 16px;
  font-weight: 600;
  font-size: 50px;
}

.topcards {
  text-align: center;
  /* margin-top: 15px; */
  color: aliceblue;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  font-weight: 500;
  font-size: 18px;
  font-style: italic;
}

.bottomcards {
  text-align: center;
  margin-top: 9px;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  color: aliceblue;
  font-weight: 500;
  font-size: 18px;
  font-style: italic;
}

@keyframes beat {
  30%,
  80% {
    transform: scale(0.92, 0.95);
  }
}
